li{
	list-style: none;
	margin: 0;
	padding: 0;
}
ul{
	margin: 0;
	padding: 0;
}

.container{
	width: 1000px;
	height: 300px;
	margin: 20px auto;
	border: solid 1px #ccc;
	position: relative;
	overflow: hidden;
}
.container:hover .prev,.container:hover .next{
	display: block;
}

.wrapp{
	height: 300px;
	position: absolute;
	left: 0;
	top: 0;
}
.wrapp li{
	/* width: 1000px; */
	height: 300px;
	float: left;
}	
.wrapp li img{
	width: 1000px;
	height: 300px;
}
.focus{
	height: 20px;
	width: 200px;
	position: absolute;
	left: 0;
	right: 0;
	bottom: 20px;
	margin: 0 auto;
	z-index: 100;
}
.focus li{
	width: 20px;
	height: 20px;
	border-radius: 50%;
	margin: 10px;
	background: #000;
	float: left;
}
.prev,.next{
	display: none;
	width: 30px;
	height: 30px;
	position: absolute;
	font-size: 16px;
	background: #999;
	line-height: 30px;
	text-align: center;
	text-decoration: none;
	color: #ccc;
	top: 50%;
	margin-top: -15px;
}


.focus .active{
	background:#ccc;
}

.prev{
	left: 0;
}
.next{
	right: 0;
}
.clearfix{
	content: "";
	display: block;
	clear: both;
}
